import React, { Component } from 'react';

import RecomendationBase from './Recommendation/RecomendationBase'
import Preferences from './Preferences'

import api from '../../util/api'

class Cabinet extends Component {

    constructor(props) {
        super(props);

        this.state = {
            pending: true,
            screen: 'list'
        }
    }

    async componentDidMount () {

        await api.get(`user`)
            .then(({data}) => {
                this.props.setUser(data)
            }).catch(() => {
                this.props.setUser(null)
                localStorage.removeItem('introViewed')
                localStorage.removeItem('Access-Token')
                delete api.defaults.headers.common['Access-Token'];
            })

        await api.get(`user/preferences`)
            .then(({data}) => {

                if (data.length) {
                    this.props.setPreferences(data)
                }
            })

        this.setState({
            pending: false
        })

    }


    render() {

        const { pending, screen } = this.state
        const { preferences, user, setToken, setUser } = this.props

        if (pending) {
            return null
        }

        if (!preferences.length || screen === 'profile') {

            return <Preferences preferences={preferences}
                                setToken={setToken}
                                setUser={setUser}
                                onSetScreen={() => {
                                    this.setState({
                                        screen: 'list'
                                    })
                                }}
                                setPreferences={this.props.setPreferences}
                                user={user}/>
        }

        return (
            <div>
                <RecomendationBase onSetScreen={() => {
                    this.setState({
                        screen: 'profile'
                    })
                }}/>
            </div>
        )
    }

}

export default Cabinet